.site-product-cat {
  .site-product-cat__header {
    border-bottom: 1px solid #d5d5d5;
    margin-bottom: 30px;
    padding-bottom: 20px;
  }
  .title-product-cat {
    font-size: 24px;
    margin: 0;
    @include flex(0 0 auto);
  }
  .pagination {
    @include flex(1);
  }
  .site-product-list {
    .item {
      margin-bottom: 30px;
      @include flexbox();
    }
  }
  .site-product-list__item {
    position: relative;
    border: 1px solid $border-color;
    @include flexbox();
    @include flex-direction(column);

    .link-product {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 5;
    }
    .item-image {
      height: 210px;
      overflow: hidden;
      padding: 15px;
      text-align: center;
      border-bottom: 1px solid $border-color;

      img {
        display: inline-block;
      }
    }
    .info-product {
      padding: 25px;
      min-height: 215px;
      @include flexbox();
      @include flex-direction(column);
      @include flex(1 auto);
    }
    .title-item-product {
      font-size: 18px;
      margin-bottom: 5px;
    }

    &:hover {
      .view-product-btn {
        &:after {
          width: 100%;
        }
      }
    }
  }
  .view-product-btn {
    position: relative;
    text-align: center;
    padding: 15px;
    color: #ffffff;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #cfcfcf;
      z-index: -2;
    }
    &:after {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      width: 0;
      height: 100%;
      z-index: -1;
      background: #ff3062;
      background: -moz-linear-gradient(-45deg, #ff3062 0%, #ff573f 100%);
      background: -webkit-linear-gradient(-45deg, #ff3062 0%,#ff573f 100%);
      background: linear-gradient(135deg, #ff3062 0%,#ff573f 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff3062', endColorstr='#ff573f',GradientType=1 );
      @include transition(all 0.4s ease-in-out);
    }
  }
}